.table-responsive {
    height:400px;  
    width:100%;
    overflow:scroll;
  }

  thead tr:nth-child(1) th{
    background: white;
    position: sticky;
    border-color: white;
    border: 10px;
    top: 0;
    z-index: 10;
  }

  .downArrow {
      width: 0;
      height: 10;
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      cursor: pointer;
      border-top: 10px solid #5036c2;
      margin-top: 50px;
      position: relative;
      top: 16px;
      left:10px

    
  }

